body {
  background-color: #b6e0f1;
  font-family: Arial, sans-serif;
}

.general {
  text-align: center;
  color: #0e11d3;
  font-size: 26px;
}

.search-form {
  max-width: 600px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.input-size {
  flex: 1;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

.button-search {
  color: #f8f404;
  cursor: pointer;
  background-color: #0e11d3;
  border: none;
  padding: 8px 16px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 16px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 4px;
}

.info {
  flex-direction: column;
  margin-top: 12px;
  display: flex;
}

.info-item {
  margin-bottom: 4px;
}

